@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@500;700&family=Poppins:wght@200;400;600&display=swap');

:root {
  --Red: hsl(0, 78%, 62%);
  --Cyan: hsl(180, 62%, 55%);
  --Orange: hsl(34, 97%, 64%);
  --Blue: hsl(212, 86%, 64%);
  --DarkBlue: hsl(234, 12%, 34%);
  --GrayishBlue: hsl(229, 6%, 66%);
  --LightGray: hsl(0, 0%, 98%);
  --mobile: 37.5em;
  --fsize: 1.5rem;
  --fw-thin: 200;
  --fw-reg: 400;
  --fw-thick: 600;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: 'Poppins', 'Courier New', Courier, monospace;
  font-size: var(--fsize);
}

.main-content {
  background-color: var(--LightGray);
  padding: 2rem 12.5% 0 12.5%;
  width: 100%;
  height: 100vh;
}
.main-heading {
  font-weight: var(--fw-reg);
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 1rem;
  line-height: 1.1;
  color: var(--GrayishBlue);
}
.bold {
  font-weight: var(--fw-thick);
  display: block;
  color: var(--DarkBlue);
}
.sec-heading {
  color: var(--GrayishBlue);
  text-align: center;
  margin-bottom: 2rem;
  font-size: var(--fsize);
  font-weight: var(--fw-reg);
}
.card-feature {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(4, 8rem);
  gap: 2rem;
}
.card {
  background-color: white;
  padding: 2rem;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  box-shadow: 2px 7px 10px rgba(61, 91, 125, 0.2);
  position: relative;
}
.card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.5rem;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}
.card--1 {
  grid-column: 2/3;
  grid-row: 1/3;
}
.card--1::before {
  background-color: var(--Red);
}
.card--2::before {
  background-color: var(--Cyan);
}
.card--3::before {
  background-color: var(--Orange);
}
.card--4::before {
  background-color: var(--Blue);
}
.card--2 {
  grid-column: 1/2;
  grid-row: 2/4;
}
.card--3 {
  grid-column: 2/3;
  grid-row: 3/5;
}
.card--4 {
  grid-column: 3/4;
  grid-row: 2/4;
}

.card__heading {
  font-size: var(--fsize);
  font-weight: var(--fw-thick);
  margin-bottom: 1rem;
}
.text {
  font-size: 1.3rem;
  color: var(--GrayishBlue);
}
.card__img {
  width: 4rem;
  align-self: end;
  margin-top: auto;
}

@media screen and (max-width: 875px) {
  html {
    font-size: 55%;
  }
  .main-content {
    padding: 3rem 7.5% 3rem 7.5%;
  }
  .main-heading {
    margin-bottom: 2rem;
    line-height: 1.3;
  }
  .sec-heading {
    margin-bottom: 3rem;
  }
  .card-feature {
    display: grid;
    gap: 3rem;
  }

  .card-feature {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 16rem);
    gap: 2rem;
  }
  .card--1 {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .card--2 {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .card--3 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .card--4 {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .card__img {
    width: 3rem;
  }
}

@media screen and (max-width: 475px) {
  .card-feature {
    display: grid;
    gap: 3rem;
  }

  .card-feature {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, 16rem);
    gap: 2rem;
    padding-bottom: 2rem;
  }
  .card--1 {
    grid-column: 1/2;
    grid-row: 1/2;
  }

  .card--2 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
  .card--3 {
    grid-column: 1/2;
    grid-row: 3/4;
  }
  .card--4 {
    grid-column: 1/2;
    grid-row: 4/5;
  }
  .main-heading {
    font-size: 2.3rem;
  }

  .sec-heading {
    text-wrap: balance;
  }
}
